<template>
  <div class="ao-asset-record">
    <ao-asset-return></ao-asset-return>
    <ao-asset-currency :title="title" :lines="lines" :unit="unit"></ao-asset-currency>
    <!-- 表格 -->
    <div class="title">{{ record_title }}</div>
    <div class="table">
      <div class="theader">
        <div class="th">
          <div class="td left">{{ record_td[0] }}</div>
          <div class="td center">{{ record_td[1] }}</div>
          <div class="td right">{{ record_td[2] }}</div>
        </div>
      </div>
      <div class="tbody">
        <div class="tr border" v-for="(item, index) in list" :key="index">
          <div class="td left">{{ ffff(item.money) }}</div>
          <div class="td center">{{ item.message }}</div>
          <div class="td right">{{ item.add_time }}</div>
        </div>
      </div>
      <div class="more" @click="more">{{ moreFlag ? $t('person.no_more') : $t('person.more') }}</div>
    </div>
  </div>
</template>

<script>
import AoAssetReturn from "./components/AoAssetReturn";
import AoAssetCurrency from "./components/AoAssetCurrency";
import { getWalletLine, getWalletLog } from "@/api/asset";

export default {
  components: {
    AoAssetReturn,
    AoAssetCurrency
  },
  data() {
    return {
      record_title: this.$t('asset.record_title'),
      record_td: this.$t('asset.record_td'),

      title: "",
      lines: [],
      money: "0.00000000",
      unit: "",
      rate: "",
      item: {},
      rateMoney: "",
      getMoney: "",
      address: "",
      number: "",
      password: "",
      wid: "",
      type: "",
      page: 1,
      moreFlag: false,
      list: []
    };
  },
  mounted() {
    var __data = JSON.parse(localStorage.getItem("jyxt_asset_table_path")); //this.$store.state.__data;
    if (!__data) {
      this.$router.back(-1);
    }
    this.unit = __data.unit;
    this.money = __data.money;
    this.ctitle = this.$t("asset.zh_title") + __data.unit + " " + __data.money;
    this.wid = __data.id;
    this.type = __data.type;
    this.currency = __data.currency;
    if (this.currency == "USDT") {
      this.item = __data;
      this.lines = [this.item];
    }
    getWalletLine({ wid: this.wid }).then(res => {
      if (res.item) {
        this.item = res.item;
        this.lines = [this.item];
      }
      this.rate = res.rate;
    });
    this.getWalletLog();
  },
  methods: {
    ffff(v, l = 8) {
      return parseFloat(v).toFixed(l);
    },
    more() {
      if (this.moreFlag) return;
      this.page++;
      this.getWalletLog();
    },
    getWalletLog() {
      getWalletLog({
        wid: this.wid,
        page: this.page,
        type: this.type,
        currency: this.currency
      }).then(res => {
        if (res.length > 0) this.list = this.list.concat(res);
        else this.moreFlag = true;
      });
    }
  }
};
</script>

<style scoped lang="scss">
.ao-asset-record {
  .title {
    color: #fff;
    margin-left: 10px;
  }
  .table {
    margin: 10px;
    padding: 0 20px;
    background: #171c2b;
    .theader,
    .tbody {
      padding: 0 20px;
    }
    .th,
    .tr {
      display: flex;
    }
    .th {
      line-height: 1.5;
    }
    .th .td,
    .tr .td {
      color: #728daf;
      font-size: 12px;
      flex: 1;
    }
    .th .td,
    .tr .td {
      &.left {
        text-align: left;
      }
      &.center {
        text-align: center;
      }
      &.right {
        text-align: right;
      }
    }
    .th .td {
      font-size: 14px;
      color: #fff;
      line-height: 3;
    }
    .tr {
      padding: 15px 0;
      &.border {
        border-top: 1px solid #1e2c42;
      }
    }
    .more {
      color: #c7cce6;
      font-size: 12px;
      width: 100%;
      line-height: 40px;
      text-align: center;
      cursor: pointer;
    }
  }
  .table.table-2 {
    .th-1 .td {
      font-size: 16px;
    }
    .th-2 .td {
      color: #637085;
      font-size: 12px;
    }
    .tr {
      border: none;
    }
    .tr .td {
      color: #fff;
      font-size: 18px;
    }
  }
}
</style>



